import { connect } from 'react-redux'
import { Dispatch } from 'redux'

function Top({state}:{state: State}) {
	const chat = state.chats[state.currentChatId]
	const savedMessages = state.myId == chat.id
	let larger = savedMessages

	let name = chat? (savedMessages? 'Saved Messages' : chat.title) : ''
	let summary = ''

	if (
		chat &&
		chat.type['@type'] == 'chatTypeSupergroup' &&
		state.supergroups[TL.chatTypeSupergroup(chat.type).supergroup_id]
	) {
		const supergroup = state.supergroups[TL.chatTypeSupergroup(chat.type).supergroup_id]
		const memberCount = supergroup.memberCount
		if (supergroup.isChannel == false) {
			summary = '' + memberCount + ' members' // TODO 1 member/ N members
			if (chat.onlineMemberCount > 1) summary += ', ' + chat.onlineMemberCount + ' online'
		}
		if (supergroup.isChannel == true) summary = '' + memberCount + ' subscribers'
	}

	const bot = (
		chat &&
		chat.type['@type'] == 'chatTypePrivate' &&
		state.users[TL.chatTypePrivate(chat.type).user_id] &&
		// TODO TL.userTypeBot(...) != null
		// ^ or better isUserTypeBot
		state.users[TL.chatTypePrivate(chat.type).user_id].type['@type'] == 'userTypeBot'
	)

	if (bot) summary = 'bot'

	if (
		chat &&
		chat.type['@type'] == 'chatTypePrivate' &&
		state.users[TL.chatTypePrivate(chat.type).user_id] &&
		state.users[TL.chatTypePrivate(chat.type).user_id].type['@type'] == 'userTypeDeleted'
	) {
		name = 'Deleted Account'
		larger = true
	}

	else

	if (
		savedMessages == false &&
		chat &&
		chat.type['@type'] == 'chatTypePrivate' &&
		state.users[TL.chatTypePrivate(chat.type).user_id] &&
		state.users[TL.chatTypePrivate(chat.type).user_id].type['@type'] == 'userTypeRegular'
	) {
		const user = state.users[TL.chatTypePrivate(chat.type).user_id]
		name = (user.firstName + ' ' + user.lastName).trim()
		if (user.status['@type'] == 'userStatusEmpty') summary = 'service notifications'
		if (user.status['@type'] == 'userStatusOnline') summary = 'online'
		if (user.status['@type'] == 'userStatusOffline') summary = 'offline'
		if (user.status['@type'] == 'userStatusRecently') summary = 'last seen recently'
		if (user.status['@type'] == 'userStatusLastWeek') summary = 'last seen within a week'
		if (user.status['@type'] == 'userStatusLastMonth') summary = 'last seen within a month'
	}

	return <div className="top">
		<div className="about">
			<div className={larger? "bold larger" : "bold"}>{name}</div>
			<div className="light">{summary}</div>
		</div>

		<div className="thinVerticalLine"/>
	</div>
}

function replaceCaret(el: HTMLElement) {
  // Place the caret at the end of the element
  const target = document.createTextNode('');
  el.appendChild(target);
  // do not move caret if element was not focused
  const isTargetFocused = document.activeElement === el;
  if (target !== null && target.nodeValue !== null && isTargetFocused) {
    var sel = window.getSelection();
    if (sel !== null) {
      var range = document.createRange();
      range.setStart(target, target.nodeValue.length);
      range.collapse(true);
      sel.removeAllRanges();
      sel.addRange(range);
    }
    if (el instanceof HTMLElement) el.focus();
  }
}

function Input({state}:{state: State}) {
	const [value, setValue] = useState('')
	const messagesEndRef = useRef(null)

	useEffect(() => {
		const current = messagesEndRef.current
		if (current) replaceCaret(current)
	})

	useEffect(() => {
		const chat = state.chats[state.currentChatId]
		// Get draft
		const draft = chat && chat.draft
		if (
			draft &&
			draft['@type'] == 'inputMessageText' &&
			TL.inputMessageText(draft).text['@type'] == 'formattedText'
		) {
			const draftText: string = TL.formattedText(TL.inputMessageText(draft).text).text
			setValue(draftText.trim())
		} else setValue('')

	}, [state.currentChatId])

	function updateValue(text: string) {
		setValue(text.trim())
	}

	function onKeyDown(e: any) {
		//console.log(e.key)
		//setValue('@@@')
		if (e.key == 'Enter') {
			tg.parseTextEntities(
				value,
				{ '@type': 'textParseModeMarkdown', version: 1 }
			).then(text => {
				tg.sendMessage(
					state.currentChatId, // chat_id
					0, // reply_to_message_id
					{
						"@type": "sendMessageOptions",
						disable_notification: false,
						from_background: false,
						scheduling_state: null as any
					},// options
					null as any,// reply_markup
					{
						'@type': 'inputMessageText',
						text: text,
						disable_web_page_preview: false,
						clear_draft: true
					}// input_message_content
				)
			})

			setValue('')
			e.preventDefault()
			return false
		}
	}

	const chat = state.chats[state.currentChatId]

	if (chat == null) return null

	if (
		chat.type['@type'] == 'chatTypePrivate' &&
		state.users[TL.chatTypePrivate(chat.type).user_id] &&
		state.users[TL.chatTypePrivate(chat.type).user_id].type['@type'] == 'userTypeDeleted'
	) {
		return null
	}

	if (
		chat.type['@type'] == 'chatTypeSupergroup' &&
		state.supergroups[TL.chatTypeSupergroup(chat.type).supergroup_id]
	) {
		const supergroup = state.supergroups[TL.chatTypeSupergroup(chat.type).supergroup_id]
		if (supergroup.isChannel == true) return null
	}

	return <div className="bottom">
		<div className="input">
			{(value == '') && <div className="placeholder">Write a message...</div>}
			<div ref={messagesEndRef} className="editor" contentEditable={true} onKeyDown={onKeyDown} onInput={e => updateValue((e.target as any).innerHTML)} dangerouslySetInnerHTML={{__html:value}}></div>
		</div>

		<div className="thinVerticalLine"/>
	</div>
}

