function DownloadsManager({state}:{state: State}) {
	//console.warn(Object.keys(state.files))
	return null
}

const CurrentChatPanel = ({state, saveChatHistory, saveFileUrl, downloadFile}:{state: State, saveChatHistory: SaveChatHistory, saveFileUrl: SaveFileUrl, downloadFile: any}) => {
	const chatSelected = state.chats[state.currentChatId] && state.chatIds.includes(state.currentChatId)
			//<div className="centerBackground" style={chatSelected? {} : {filter: 'blur(30px)'}}></div>
	return <>
		<DownloadsManager state={state}/>
		<div className="blow center">
		{
			// TODO handle status . left the group
			chatSelected?
				<>
					<Top state={state}/>
					<History state={state} saveChatHistory={saveChatHistory} {...{downloadFile}} saveFileUrl={saveFileUrl}/>
					<Input state={state}/>
				</>
			:
				<>
					<div className="pleaseSelectChat "><div>Please select a chat to start messaging</div></div>
					<div className="pleaseSelectChat "><a href="https://www.patreon.com/PeyTy">Made for you by PeyTy</a></div>
				</>
		}
		</div>
	</>
}

type SaveChatHistory = (id: number, messages: ReadonlyArray<TL.TLMessage>) => void
type SaveFileUrl = (id: number, url: string) => void

const mapStateToProps = (state: State, ownProps: any) => ({ state })

function saveFileUrl(id: number, url: string) {
	return async (dispatch:Dispatch, getState: () => State) => {
		console.error('SAVE_FILE_URL from thunk', id, url)
		dispatch({ type: 'SAVE_FILE_URL', payload: { id, url } })
	}
}

let busy = false
// TODO rename to downloadPhoto cause it creates blob URL?
// TODO Take TLFile instead of raw id
export function downloadFile(idOrFile: number | TL.TLFile) {
	return async (dispatch:Dispatch, getState: () => State) => {
		if (busy) return;
		//;(window as any).downloadFileBusy = (window as any).downloadFileBusy || 0
		//if ((window as any).downloadFileBusy > 1) alert('downloadFileBusy = ' + (window as any).downloadFileBusy)
		//if ((window as any).downloadFileBusy < 0) alert('downloadFileBusy<0 = ' + (window as any).downloadFileBusy)
		//if ((window as any).downloadFileBusy == 1) return;

		//console.error('SAVE_FILE_URL from thunk', id, url)
		const id = typeof(idOrFile) == 'number'? idOrFile as number : (idOrFile as TL.TLFile).id
		//console.warn('downloadFile from thunk', id)

		const state = getState()

		//if (state.fileLoads[id] == true) {
		//	console.warn('downloadFile from thunk already loads', id)
		//	return
		//}

		//dispatch({ type: 'SAVE_FILE_LOADS', payload: { id, loads: true } })

		//console.warn('downloadFile from thunk starts loading', id)

		const file: TL.TLFile | null = state.files[id] ?? ((typeof(idOrFile) != 'number') && (idOrFile as TL.TLFile))

		if (file == null) return console.warn('downloadFile->state.files[id] == null')
		if (state.fileURL[id] != null) return

		if (
			state.fileLoads[id] == null &&
			//state.files[id] &&
			//state.files[id].local.can_be_downloaded == true &&
			//state.files[id].local.is_downloading_completed == false &&
			//state.files[id].local.is_downloading_active == false

			file &&
			file.local.can_be_downloaded == true &&
			file.local.is_downloading_completed == false &&
			file.local.is_downloading_active == false
		) {
			//;(window as any).downloadFileBusy++
			//console.warn('downloadFile->downloadFile', id, state.fileLoads[id])
			dispatch({ type: 'SAVE_FILE_LOADS', payload: { id, loads: 1 } })
			//console.warn('downloadFile->downloadFile restate', id, getState().fileLoads[id])
			const file = await tg.downloadFile(id, 30, 0, 0, false)
			// TODO try
			//console.error('Handle this file please (not critical)', file)
			//alert('oops')
			return
		}

		//await new Promise(resolve => setTimeout(resolve, 1000))

		if (
			(state.fileLoads[id] == null || state.fileLoads[id] == 1) &&
			//state.files[id] &&
			//state.files[id].local.is_downloading_completed == true

			file &&
			file.local.is_downloading_completed == true
		) {
			//console.warn('downloadFile->readFilePart', id)
			if (busy) return;
			busy = true
			dispatch({ type: 'SAVE_FILE_LOADS', payload: { id, loads: 2 } })
			//while (busy)
			const filePart = await tg.readFilePart(id, 0, 0)
			// TODO try
			await new Promise(resolve => setTimeout(resolve, 128))
			let blobURL = (window as any).blobURL || window.URL.createObjectURL(filePart.data)
			//;(window as any).downloadFileBusy--
			//;(window as any).blobURL = blobURL
			busy = false
			dispatch({ type: 'SAVE_FILE_URL', payload: { id, url: blobURL } })
		}
	}
}

const mapDispatchToProps = (dispatch:Dispatch) => {
	return {
		saveChatHistory: (id: number, messages: ReadonlyArray<TL.TLMessage>) => {
			return dispatch({ type: 'SAVE_CHAT_HISTORY', payload: { chat_id: id, messages } })
		},
		saveFileUrl: (id: number, url: string) => {
			return dispatch(saveFileUrl(id, url) as any)
			//return dispatch({ type: 'SAVE_FILE_URL', payload: { id, url } })
		},
		downloadFile: (id: number) => {
			//console.error('SAVE_FILE_URL from thunk', id, url)
			return dispatch(downloadFile(id) as any)
			//return dispatch({ type: 'SAVE_FILE_URL', payload: { id, url } })
		},
	}
}

const CurrentChatPanelConnected = connect(mapStateToProps, mapDispatchToProps)(CurrentChatPanel)

export { CurrentChatPanelConnected as CurrentChatPanel }
