export interface ChatPhoto {

}

export interface Chat {
	readonly title: string
	readonly id: number
	readonly isPinned: boolean
	readonly unreadCount: number // "updateUnreadChatCount" - TODO
	readonly photo: TL.TLChatPhoto | null
	readonly lastMessage: number // "updateChatLastMessage" - TODO
	readonly type: TL.TLChatType
	readonly onlineMemberCount: number

	readonly replyToMessageIdDraft: number // No considered if draft == null
	readonly draft: TL.TLInputMessageContent | null
}

export interface Supergroup {
	readonly chatMemberStatus: TL.TLChatMemberStatus
	readonly isChannel: boolean
	readonly isVerified: boolean
	readonly memberCount: number
	readonly username: string
}

export enum LoginState {
  WaitTDLib,
  WaitPhoneNumber,
  WaitCode,
  WaitRegistration,
  WaitPassword,
  Ready
}

export interface State {
	// GUI
	readonly loaded: boolean
	readonly showSideBar: boolean
	readonly loginState: LoginState
	readonly myId: number
	readonly currentChatId: number

	// Opened dialogs
	readonly chatIds: ReadonlyArray<number>
	readonly chats: { readonly [chat_id: number]: Chat }
	readonly users: { readonly [user_id: number]: User }
	readonly messages: {
		readonly [chat_id: number]: { readonly [message_id: number]: Message }
	}
	readonly supergroups: { readonly [supergroup_id: number]: Supergroup }
	readonly history: { readonly [chat_id: number]: number[] }

	// Files
	readonly files: { readonly [file_id: number]: TL.TLFile }
	readonly fileURL: { readonly [file_id: number]: string }
	// If true, then downloadFile & readFilePart should NOT be repeated
	readonly fileLoads: { readonly [file_id: number]: number }
}

//type TelegramStates = const { }

const initialState: State = {
	loaded: false,
	loginState: LoginState.WaitTDLib,
	showSideBar: false,
	myId: 0,
	currentChatId: 0,
	chatIds: [],
	chats: {},
	users: {},
	messages: {},
	supergroups: {},
	files: {},
	fileURL: {},
	fileLoads: {},
	history: {},
}

