export interface TDLIB_UPDATE {
	readonly type: 'TDLIB_UPDATE'
	readonly payload: TL.TLObject
}

export interface TDLIB_MANUAL_UPDATE {
	readonly type: 'TDLIB_MANUAL_UPDATE'
	readonly payload: TL.TLObject | null
}

export interface SELECT_CHAT {
	readonly type: 'SELECT_CHAT'
	readonly payload: number
}

export interface SAVE_CHAT_HISTORY {
	readonly type: 'SAVE_CHAT_HISTORY'
	readonly payload: {
		chat_id: number,
		messages: ReadonlyArray<TL.TLMessage>
	}
}

export interface SAVE_FILE_URL {
	readonly type: 'SAVE_FILE_URL'
	readonly payload: {
		id: number,
		url: string
	}
}

export interface SAVE_FILE_LOADS {
	readonly type: 'SAVE_FILE_LOADS'
	readonly payload: {
		id: number,
		loads: number
	}
}

export interface SET_SIDEBAR_VISIBILITY {
	readonly type: 'SET_SIDEBAR_VISIBILITY'
	readonly payload: {
		showSideBar: boolean
	}
}

interface INIT {
	readonly type: '@@INIT'
}

export type ActionTypes =
	INIT |
	TDLIB_UPDATE |
	SELECT_CHAT |
	SAVE_CHAT_HISTORY |
	SAVE_FILE_URL |
	SAVE_FILE_LOADS |
	SET_SIDEBAR_VISIBILITY |
	TDLIB_MANUAL_UPDATE

function chatToState(tl: TL.TLChat): Chat {
	return {
		title: tl.title,
		id: tl.id,
		isPinned: tl.is_pinned,
		unreadCount: tl.unread_count,
		photo: tl.photo,
		lastMessage: 0,
		onlineMemberCount: 0,
		replyToMessageIdDraft: tl.draft_message? tl.draft_message.reply_to_message_id : 0,
		draft: tl.draft_message? tl.draft_message.input_message_text : null,
		type: tl.type,
		//chatMemberStatus: tl.status,
	}
}

function userToState(tl: TL.TLUser): User {
	return {
		id: tl.id,
		firstName: tl.first_name, // TODO .trim()
		// TODO combinedName = +' '+
		// TODO Dell Acc name
		lastName: tl.last_name ?? '',
		username: tl.username,
		phone: tl.phone_number ?? '',
		type: tl.type,
		status: tl.status,
		verified: tl.is_verified,
		photo: tl.profile_photo
	}
}

function supergroupToState(tl: TL.TLSupergroup) {
	return {
		id: tl.id,
		chatMemberStatus: tl.status,
		isChannel: tl.is_channel,
		isVerified: tl.is_verified,
		memberCount: tl.member_count,
		username: tl.username,
	}
}

